.pack {
    
    font-family: Arial, sans-serif;
    background-color: #1a1a1a;
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    margin: 0;

} 
.container {
    
    width: 350px;
    background: #333;
    border-radius: 10px;
    padding: 20px;
    text-align: center;
}
.form-container {
    text-align: left;
}
.form-container label {
    display: block;
    margin-bottom: 5px;
}
.form-container input {
    width: calc(100% - 22px);
    padding: 10px;
    margin-bottom: 10px;
    border: none;
    border-radius: 5px;
    color: black; /* Set input text color to black */
}
.form-container input[type="file"] {
    padding: 0;
}
.form-container button {
    width: 100%;
    padding: 10px;
    border: none;
    background: #ff9900;
    color: #fff;
    font-size: 16px;
    border-radius: 5px;
    cursor: pointer;
}
